@import url(https://fonts.googleapis.com/css2?family=Eczar:wght@400;500;600;700;800&display=swap);
html,
body,
#root {
  margin: 0;
  width: 100%;
  height: 100%;
}

html,
body {
  font-family: "Eczar", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.MenuButton {
  opacity: 0;
  cursor: pointer;
  font-weight: 500;
  color: #fff;
  font-size: 36px;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.MenuButton.ContactMe {
  justify-self: flex-end;
}

.MenuButton > button {
  cursor: pointer;
  font-size: 100%;
  font-family: inherit;
  border: 0;
  padding: 0;
  text-transform: none;
  overflow: visible;
  background: none;
  color: inherit;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  border: none;
  margin: 0 20px;
}

.MenuButton:not(.Challenge) > .Square {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 5px;
  -webkit-animation: 2s linear rotate;
          animation: 2s linear rotate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  display: none;
}

.MenuButton.Challenge > .Square {
  width: 40px;
  height: 40px;
  margin-top: -1px;
  background-size: 80%;
  background-repeat: no-repeat;
  background-position-x: 4px;
  background-position-y: 7px;
  display: none;
}

.MenuButton.Challenge > .Square.Right {
  -webkit-animation: 2s linear rotate-inverse;
          animation: 2s linear rotate-inverse;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.MenuButton.Challenge > .Square.Left {
  -webkit-animation: 2s linear rotate;
          animation: 2s linear rotate;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.MenuButton:hover > button {
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
}

.MenuButton:hover > .Square {
  display: block;
}

@-webkit-keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(1turn);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(1turn);
  }
}

@-webkit-keyframes rotate-inverse {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-1turn);
  }
}

@keyframes rotate-inverse {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(-1turn);
  }
}

.Menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: calc(100% - 40px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
  z-index: 100;
  transition: transform 1s;
  transform: translateX(-100%);
}

.Menu.Open {
  transform: translateX(0%);
}

.MenuBackground {
  opacity: 0.75;
  background-color: #a3403b;
  width: 100%;
  height: 100%;
  position: absolute;
  margin-top: -20px;
}

.Menu > *:not(.MenuBackground) {
  margin: 10px 0px;
  z-index: 10;
}

.Menu > img {
  width: 90%;
  max-width: 200px;
  height: auto;
  -webkit-clip-path: circle(45% at center);
          clip-path: circle(45% at center);
}

@media screen and (min-width: 601px) and (max-width: 1000px) {
  .Menu {
    height: calc(100% - 20px);
    padding: 10px;
  }

  .MenuBackground {
    margin-top: -10px;
  }
}

@media screen and (max-width: 600px) {
  .Menu {
    width: 100%;
    height: 100%;
    max-width: 100%;
    padding: 0px;
  }

  .MenuBackground {
    height: 100%;
    margin-top: 0;
    opacity: 1;
  }
}

.Burger {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: pointer;
  z-index: 200;
}

.Burger > div {
  width: 40px;
  height: 5px;
  border-radius: 5px;
  background-color: #fff;
  margin: 6px 0px;
  transition: transform 0.5s;
}

.Burger.Open > .Top {
  transform: rotate(45deg) translateX(8px) translateY(8px);
}

.Burger.Open > .Bottom {
  transform: rotate(-45deg) translateX(7px) translateY(-7px);
}

.Burger.Open > .Middle {
  transform: scaleX(0) scaleY(0) translateX(-200%);
}

.ContactIcon {
  width: 40px;
  height: 40px;
  padding: 5px;
  display: block;
}

@media screen and (max-width: 1024px) {
  .ContactIcon {
    display: inline-block;
  }
}

.ContactIcon > img {
  width: 40px;
  height: 40px;
}

@media screen and (max-width: 512px) {
  .ContactIcon {
    width: 20px;
    height: 20px;
    padding: 2px;
  }
  .ContactIcon > img {
    width: 20px;
    height: 20px;
  }
}

.ContactIcon:hover {
  -webkit-animation: shake 0.5s infinite;
          animation: shake 0.5s infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes shake {
  0% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(15deg);
  }
}

@keyframes shake {
  0% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(15deg);
  }
}

.ContactIcons {
  right: 10px;
  bottom: 10px;
  position: absolute;
  z-index: inherit;
}

@media screen and (max-width: 512px) {
  .ContactIcons {
    right: 5px;
    bottom: 0px;
  }
}

.Page {
  width: 100%;
  height: 100%;
  padding: 80px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  .Page {
    padding: 60px;
  }
}

.ModalContainer {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.Modal {
  max-width: 900px;
  max-height: 100%;
  font-size: 24px;
  opacity: 0;
  text-align: center;
  color: #ffffff;
  padding: 10px;
  margin: 10px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 15px;
  z-index: 15;
  box-sizing: border-box;
}

@media screen and (max-width: 1024px) {
  .Modal {
    max-width: 100%;
    margin: 0px;
  }
}

.Modal > p {
  margin: 0;
  padding: 0;
  width: auto;
  height: auto;
}

.Modal > button {
  outline: none;
  background: none;
  font: inherit;
  color: #fff;
  border: 5px solid #fff;
  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
  padding: 15px;
}

.Modal > button:active {
  padding: 10px;
  margin: 5px;
}

.Modal a {
  color: #61f5ee;
}

.Modal a:visited {
  color: #f576f5;
}

@media screen and (max-width: 512px) {
  .Modal > p {
    font-size: 0.75em;
  }

  .Modal > button {
    font-size: 0.75em;
    border: 5px solid #fff;
    padding: 10px;
  }

  .Modal > button:active {
    padding: 5px;
    margin: 5px;
  }
}

.Modal.Backless {
  background: rgba(255, 255, 255, 0);
  border-radius: 15px;
}

.Modal.Ghost {
  opacity: 0;
  transform: translateX(-20px);
}

.Carousel {
  width: 100%;
  overflow: hidden;
  z-index: 5;
}

.Carousel > button {
  position: absolute;
  margin-top: auto;
  background: none;
  border: none;
  outline: none;
  z-index: 10;
}

.Carousel > button > .chevron {
  background: #fff;
  height: 15px;
  width: 50px;
  border-radius: 5px;
  cursor: pointer;
}

.Carousel > button:active {
  opacity: 0.75;
}

.Carousel > button.LeftArrow > .chevron.top {
  transform: rotate(-60deg) translateX(5px) translateY(-10px);
}

.Carousel > button.LeftArrow > .chevron.bottom {
  transform: rotate(60deg) translateX(5px) translateY(10px);
}

.Carousel > button.RightArrow > .chevron.top {
  transform: rotate(60deg) translateX(-5px) translateY(-10px);
}

.Carousel > button.RightArrow > .chevron.bottom {
  transform: rotate(-60deg) translateX(-5px) translateY(10px);
}

.Carousel > .LeftArrow {
  left: 15px;
  top: calc(50% - 25px);
}

.Carousel > .RightArrow {
  right: 15px;
  top: calc(50% - 25px);
}

@media screen and (max-width: 512px) {
  .Carousel > .LeftArrow {
    left: -55px;
  }

  .Carousel > .RightArrow {
    right: -55px;
  }
}
.CarouselTrack {
  display: flex;
  height: 100%;
  transition: transform 2s;
  white-space: nowrap;
  align-items: stretch;
}

.Pane {
  display: inline-flex;
  width: 100%;
  vertical-align: top;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Pane > h2 {
  margin: 20px 0;
  white-space: pre-line;
}

@media screen and (max-width: 512px) {
  .Pane > h2 {
    font-size: 1em;
    margin: 5px 0;
  }
}

.Pane > img {
  max-width: 100%;
  max-height: 300px;
}

@media screen and (max-width: 512px) {
  .Pane > img {
    max-width: 80%;
    max-height: 200px;
  }
}

.Pane > section > p {
  white-space: pre-line;
  padding: 0 50px;
}

@media screen and (max-width: 512px) {
  .Pane > section > p {
    font-size: 0.75em;
    padding: 0 15px;
    margin: 5px 0;
  }
}

.CookieBanner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 200px;
  position: fixed;
  bottom: 0px;
  transition: transform 2s;
  transform: translateY(100%);
}

@media screen and (max-width: 512) {
  .CookieBanner {
    height: 100px;
  }
  .CookieBanner > p {
    margin: 5px 0;
  }
}

.App {
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: 100;
  overflow: hidden;
}

.App.Vanish {
  transition: opacity 10s;
  opacity: 0;
}

.Background {
  background-color: #000b2e;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
}

.Sprites {
  z-index: -1;
}

.Sprite {
  position: fixed;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: #fff;
  z-index: 10;
  opacity: 0;
}

